@charset "UTF-8";
.FollowerRcmd {
  background-color: #fff;
  padding-bottom: 3px !important;
  margin-bottom: 0px !important; }
  .FollowerRcmd .titleArea {
    margin-bottom: 5px !important; }
    .FollowerRcmd .titleArea .rcmd {
      font-size: 1.28em;
      line-height: 1.28em;
      margin-right: 3px; }
    .FollowerRcmd .titleArea .refresh {
      font-size: 0.88em;
      line-height: 0.88em;
      color: orange;
      margin-right: 3px;
      margin-left: 3px; }
    .FollowerRcmd .titleArea .seeall {
      margin-left: 3px;
      font-size: 0.88em;
      line-height: 0.88em;
      color: orange; }
  .FollowerRcmd .rcmdCol {
    padding-top: 5px !important;
    padding-bottom: 0px !important; }
    .FollowerRcmd .rcmdCol:nth-child(2)::before {
      width: 0% !important; }
    .FollowerRcmd .rcmdCol .avatar {
      margin-top: 10px !important;
      margin-bottom: 10px !important;
      transform: scale(1.16);
      cursor: pointer;
      position: relative !important; }
      .FollowerRcmd .rcmdCol .avatar img {
        width: 45px;
        height: 45px;
        min-width: 45px; }
      .FollowerRcmd .rcmdCol .avatar:hover ~ .avatarInfo .nameArea > a > .nickname {
        color: orange;
        text-decoration: underline; }
    .FollowerRcmd .rcmdCol .avatarInfo {
      position: relative;
      padding-left: 0px !important;
      margin-left: 0px !important;
      margin-top: 10px !important;
      margin-bottom: 10px !important;
      width: 100% !important; }
      .FollowerRcmd .rcmdCol .avatarInfo .nameArea {
        position: relative !important;
        margin-top: -2px;
        margin-bottom: 2px;
        width: 100% !important;
        white-space: nowrap;
        overflow: hidden; }
        .FollowerRcmd .rcmdCol .avatarInfo .nameArea a {
          color: #000; }
          .FollowerRcmd .rcmdCol .avatarInfo .nameArea a:hover .nickname {
            color: orange;
            text-decoration: underline; }
        .FollowerRcmd .rcmdCol .avatarInfo .nameArea .nickname {
          margin-right: 5px; }
        .FollowerRcmd .rcmdCol .avatarInfo .nameArea .idname {
          margin-right: 5px;
          color: rgba(0, 0, 0, 0.5); }
        .FollowerRcmd .rcmdCol .avatarInfo .nameArea .close {
          color: rgba(0, 0, 0, 0.16);
          position: absolute;
          right: 0px;
          font-size: 1.2em;
          font-weight: 600 !important;
          cursor: pointer;
          user-select: none; }
      .FollowerRcmd .rcmdCol .avatarInfo .followBt {
        border: 0.5px solid orange;
        color: orange;
        font-weight: 600 !important;
        position: relative !important;
        width: 90px !important;
        text-shadow: 0px 0px 2px rgba(0, 0, 0, 0.5) !important;
        padding: 8px 16px !important;
        font-size: 12px !important;
        line-height: 12px !important;
        user-select: none;
        white-space: nowrap; }
        .FollowerRcmd .rcmdCol .avatarInfo .followBt:hover {
          background-color: rgba(0, 0, 0, 0.05) !important; }
      .FollowerRcmd .rcmdCol .avatarInfo .followed {
        background-color: orange !important;
        color: white !important;
        user-select: none; }
        .FollowerRcmd .rcmdCol .avatarInfo .followed:hover:before {
          content: '언팔로우';
          position: absolute !important;
          width: 100%;
          height: 100%;
          z-index: 10;
          left: 0px;
          top: 0px;
          background-color: #AC002B !important;
          color: white !important;
          display: flex;
          justify-content: center;
          align-items: center;
          user-select: none;
          pointer-events: none; }
        .FollowerRcmd .rcmdCol .avatarInfo .followed:focus {
          background-color: orange !important;
          color: white !important; }
    .FollowerRcmd .rcmdCol:after {
      content: '';
      position: absolute;
      z-index: -1;
      height: 100%;
      width: 100%;
      background-color: white;
      top: 0px;
      opacity: 0;
      transition: all 0.1s; }
  .FollowerRcmd .lastCol {
    border-bottom: 1px solid #E6ECF0;
    z-index: 2; }
  .FollowerRcmd .whiteBoard:after {
    content: '';
    position: absolute;
    z-index: 10;
    height: 100%;
    width: 100%;
    background-color: white;
    top: 0px;
    opacity: 1;
    transition: all 0.1s; }
  .FollowerRcmd .firstPart {
    padding-top: 0px !important;
    padding-bottom: 0px !important; }
    .FollowerRcmd .firstPart a {
      color: #000; }
    .FollowerRcmd .firstPart .firstPartColImg .imgWrapper {
      display: flex;
      justify-content: center;
      align-items: center;
      border: 1.5px solid #E6ECF0;
      position: relative;
      height: 34px;
      width: 38px;
      border-radius: 3px; }
      .FollowerRcmd .firstPart .firstPartColImg .imgWrapper .imageMail {
        background-color: red;
        width: 24px;
        height: 24px;
        background: transparent url(./web_sprite.png) no-repeat;
        background-position: 0px 0px !important; }
    .FollowerRcmd .firstPart .firstPartColTxt {
      margin-left: -3px; }
      .FollowerRcmd .firstPart .firstPartColTxt .littletitle {
        color: #657786;
        font-size: 0.88em;
        line-height: 0.88em; }
  .FollowerRcmd .secondPart {
    padding-top: 0px !important;
    padding-bottom: 0px !important;
    color: orange;
    font-size: 0.88em; }
    .FollowerRcmd .secondPart a {
      color: orange; }
      .FollowerRcmd .secondPart a:hover {
        text-decoration: underline; }
    .FollowerRcmd .secondPart .secondPartCol {
      margin-top: 10px !important;
      margin-bottom: 10px !important; }
